
.home{
    background-color: #E6ECF0;
    height: 100%;
}

.home-body{
    display: flex;
    flex-direction: row;
    flex: 1;
}

.home-header {    
    height: 46px;
    background-color: white;
    display: flex;
    align-items: center;    
    border-bottom: 1px solid #ABB8C3;
}

.home-header-left-button{
    color: #66757f;    
    font-weight: bold;
    font-size: 14px;
    height: 100%;    
    display: flex; 
    align-items: center;    
    margin-left: 20px;
}

.home-header-left-button:first-child{
    margin-left: 80px;
}

.home-header-left-button-active{
    color: #ABB8C3;
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;   
    border-bottom: 3px solid #ABB8C3;
}

.home-header-left-button:hover{
    cursor: pointer;
    color: #ABB8C3;
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;   
    border-bottom: 3px solid #ABB8C3;
}

.home-header-search {    
    position: absolute;        
    right: 80px;
}

.home-header-right-button {    
    width: 50px;
    position: absolute;        
    right: 80px;
    margin-top: 0px;    
}

.home-left {
    min-width: 290px;   
    margin: 5px 5px 5px 80px;
}

.home-middle {
    min-width: 590px;    
    margin: 5px 5px 5px 5px;
}

.home-right {
    min-width: 290px;
    margin: 5px 80px 5px 5px;
}