.profile-image{        
    border: 3px solid white;    
    border-radius: 50%;
    background-color: #ABB8C2;
    display: flex;    
    justify-content: center;
    
}

.profile-image:hover{
    cursor: pointer;
}

.profile-image-svg{
    fill: white;         
    border-radius: 50px;
}

.profile-input{
    display: none;
}