.button{
    border: 1px solid #1da1f2;
    border-radius: 5px;
    width: 100%;
    padding: 5px;    
    margin-top: 5px;
    margin-bottom: 5px;            
}

.button-loading{
    border: 1px solid #ABB8C2;
    color:#ABB8C2 !important;  
    display: flex;
    width: 100% !important; 
    align-items: center;
    justify-content: center;
}

.button-loading-space{
    margin-right: 5px;
}

.button:hover{
    cursor: pointer;
}

.button-normal {    
    background-color: #1da1f2;
    color: white;
}

.button-outline {
    background-color: white;
    color: #1da1f2;    
}