body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root, .App { margin:0; height: auto;font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;}

.loader {
    position: relative;
    border: 3px solid #f3f3f3; /* Light grey */
    border-top: 3px solid #1da1f2; /* Blue */
    border-radius: 50%;    
    width: 15px;
    height: 15px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;    
  }
  
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

  .loader-centralized {
    margin-left: -25px;    
    top: 50%;
    left: 50%;  
    width: 25px;
    height: 25px;    
  }
.button{
    border: 1px solid #1da1f2;
    border-radius: 5px;
    width: 100%;
    padding: 5px;    
    margin-top: 5px;
    margin-bottom: 5px;            
}

.button-loading{
    border: 1px solid #ABB8C2;
    color:#ABB8C2 !important;  
    display: flex;
    width: 100% !important; 
    align-items: center;
    justify-content: center;
}

.button-loading-space{
    margin-right: 5px;
}

.button:hover{
    cursor: pointer;
}

.button-normal {    
    background-color: #1da1f2;
    color: white;
}

.button-outline {
    background-color: white;
    color: #1da1f2;    
}
.input {
    width: 100%;   
    padding: 5px; 
    margin-top: 5px;
    margin-bottom: 5px;
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;   
    border: 1px solid gray;
    border-radius: 5px;
}

.input-error {
    border-color: red;
    margin-bottom: -5px;
}

.input-error-label {    
    color: red;
    font-size: 10px;
    text-align: left !important;
}



.profile{       
    position: relative;     
    height: 200px;    
    background-color: white;
}

.profile-cover{
    height: 100px;
    background-color: #ABB8C2;

}

.profile-cover:hover{
    cursor: pointer;
}

.profile-cover-image{    
    width: 290px;
    height: 100px;    
}

.profile-cover-input{
    display: none;
}
.profile-image-absolute{    
    position: absolute;
    width: 70px;
    height: 70px;
    top: 60px;
    margin-left: 10px;
}

.profile-name-absolute{        
    margin-left: 90px;    
    margin-top: 5px;    
}

.profile-numbers{    
    display: flex;    
    position: absolute;    
    bottom:5px;        
    margin-left: 10px;    
}

.profile-numbers-column{
    display: flex;
    flex-direction: column;
    margin-right: 10px;     
}

.profile-numbers-column-title{
    font-weight: bold;  
    margin-bottom:5px;
    color: #66757f;
}

.profile-numbers-column-value{
    color: #ABB8C2;    
}
.profile-image{        
    border: 3px solid white;    
    border-radius: 50%;
    background-color: #ABB8C2;
    display: flex;    
    justify-content: center;
    
}

.profile-image:hover{
    cursor: pointer;
}

.profile-image-svg{
    fill: white;         
    border-radius: 50px;
}

.profile-input{
    display: none;
}
.profile-name{    
    display: flex;
    flex-direction: column;        
    font-weight: bold;    
}

.profile-name:hover{    
    cursor: pointer;
}

.profile-name-line{        
    margin-left: 10px;
    flex-direction: row;        
    align-items: center;    
}

.profile-tag{
    font-size: 14px;
    font-weight: normal;
    margin-top: 5px;
    color: #66757f;
}

.profile-tag-line{        
    margin-left: 5px;
}
.post{
    position: relative;
    padding: 10px;
    border-top: 1px solid #ABB8C2;
}

.post-profile{
    position: relative;
    display: flex;
    align-items: center;    
}

.post-user{
    display: flex;
    flex-direction: column;
    margin-left: 5px;
}
.post-image{       
    width: 50px;
    height: 50px;        
}

.post-text{
    margin-top: 10px;
}

.post-date{
    margin-top: 10px;
    font-size: 12px;
}
.timeline{    
    padding-top: 10px;
    padding-bottom: 10px;
    position: relative;    
    background-color: white;
}

.timeline-input{
    margin-top: 0px;
    width: 100%;
    resize: none;
    border-radius: 10px;        
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;   
    min-height: 30px;
    line-height: 20px;    
}

.timeline-form{
     padding: 10px;     
}

.timeline-tweet-button{    
    width: 60px; 
    float: right;   
}

.timeline-posts{
    margin-top: 40px;
}

.friends{
    position: relative;
    padding: 10px;  
    background-color: white;
}

.friends-text{
    margin-bottom: 15px;
}

.friends-profile{
    position: relative;
    display: flex;
    align-items: center;    
    margin-bottom: 10px;    
    padding-bottom: 10px;
    border-bottom: 1px solid #ABB8C2;
}

.friends-profile:last-child{
    border-bottom: none;
}

.friends-user{    
    margin-left: 10px;
}

.friends-tag{
    font-size: 12px;
    margin-left: 10px;
}
.friends-image{       
    width: 50px;
    height: 50px;
    border: 3px solid white;
    top: 60px;
    border-radius: 50%;
    background-color: #ABB8C2;
    display: flex;    
    justify-content: center;
}

.friends-follow{
    width: 60px;
    margin-left: 10px;
}

.login {
    width: 400px;     
    height: 200px;
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;    
}
.login  p {
    text-align: center;
}


.signup {
    width: 400px;      
    height: 200px;
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;   
}
.signup  p {
    text-align: center;
}



.home{
    background-color: #E6ECF0;
    height: 100%;
}

.home-body{
    display: flex;
    flex-direction: row;
    flex: 1 1;
}

.home-header {    
    height: 46px;
    background-color: white;
    display: flex;
    align-items: center;    
    border-bottom: 1px solid #ABB8C3;
}

.home-header-left-button{
    color: #66757f;    
    font-weight: bold;
    font-size: 14px;
    height: 100%;    
    display: flex; 
    align-items: center;    
    margin-left: 20px;
}

.home-header-left-button:first-child{
    margin-left: 80px;
}

.home-header-left-button-active{
    color: #ABB8C3;
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;   
    border-bottom: 3px solid #ABB8C3;
}

.home-header-left-button:hover{
    cursor: pointer;
    color: #ABB8C3;
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;   
    border-bottom: 3px solid #ABB8C3;
}

.home-header-search {    
    position: absolute;        
    right: 80px;
}

.home-header-right-button {    
    width: 50px;
    position: absolute;        
    right: 80px;
    margin-top: 0px;    
}

.home-left {
    min-width: 290px;   
    margin: 5px 5px 5px 80px;
}

.home-middle {
    min-width: 590px;    
    margin: 5px 5px 5px 5px;
}

.home-right {
    min-width: 290px;
    margin: 5px 80px 5px 5px;
}
