.loader {
    position: relative;
    border: 3px solid #f3f3f3; /* Light grey */
    border-top: 3px solid #1da1f2; /* Blue */
    border-radius: 50%;    
    width: 15px;
    height: 15px;
    animation: spin 1s linear infinite;    
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

  .loader-centralized {
    margin-left: -25px;    
    top: 50%;
    left: 50%;  
    width: 25px;
    height: 25px;    
  }