.profile{       
    position: relative;     
    height: 200px;    
    background-color: white;
}

.profile-cover{
    height: 100px;
    background-color: #ABB8C2;

}

.profile-cover:hover{
    cursor: pointer;
}

.profile-cover-image{    
    width: 290px;
    height: 100px;    
}

.profile-cover-input{
    display: none;
}
.profile-image-absolute{    
    position: absolute;
    width: 70px;
    height: 70px;
    top: 60px;
    margin-left: 10px;
}

.profile-name-absolute{        
    margin-left: 90px;    
    margin-top: 5px;    
}

.profile-numbers{    
    display: flex;    
    position: absolute;    
    bottom:5px;        
    margin-left: 10px;    
}

.profile-numbers-column{
    display: flex;
    flex-direction: column;
    margin-right: 10px;     
}

.profile-numbers-column-title{
    font-weight: bold;  
    margin-bottom:5px;
    color: #66757f;
}

.profile-numbers-column-value{
    color: #ABB8C2;    
}