.friends{
    position: relative;
    padding: 10px;  
    background-color: white;
}

.friends-text{
    margin-bottom: 15px;
}

.friends-profile{
    position: relative;
    display: flex;
    align-items: center;    
    margin-bottom: 10px;    
    padding-bottom: 10px;
    border-bottom: 1px solid #ABB8C2;
}

.friends-profile:last-child{
    border-bottom: none;
}

.friends-user{    
    margin-left: 10px;
}

.friends-tag{
    font-size: 12px;
    margin-left: 10px;
}
.friends-image{       
    width: 50px;
    height: 50px;
    border: 3px solid white;
    top: 60px;
    border-radius: 50%;
    background-color: #ABB8C2;
    display: flex;    
    justify-content: center;
}

.friends-follow{
    width: 60px;
    margin-left: 10px;
}
