.profile-name{    
    display: flex;
    flex-direction: column;        
    font-weight: bold;    
}

.profile-name:hover{    
    cursor: pointer;
}

.profile-name-line{        
    margin-left: 10px;
    flex-direction: row;        
    align-items: center;    
}

.profile-tag{
    font-size: 14px;
    font-weight: normal;
    margin-top: 5px;
    color: #66757f;
}

.profile-tag-line{        
    margin-left: 5px;
}