.input {
    width: 100%;   
    padding: 5px; 
    margin-top: 5px;
    margin-bottom: 5px;
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;   
    border: 1px solid gray;
    border-radius: 5px;
}

.input-error {
    border-color: red;
    margin-bottom: -5px;
}

.input-error-label {    
    color: red;
    font-size: 10px;
    text-align: left !important;
}


