.timeline{    
    padding-top: 10px;
    padding-bottom: 10px;
    position: relative;    
    background-color: white;
}

.timeline-input{
    margin-top: 0px;
    width: 100%;
    resize: none;
    border-radius: 10px;        
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;   
    min-height: 30px;
    line-height: 20px;    
}

.timeline-form{
     padding: 10px;     
}

.timeline-tweet-button{    
    width: 60px; 
    float: right;   
}

.timeline-posts{
    margin-top: 40px;
}
