.post{
    position: relative;
    padding: 10px;
    border-top: 1px solid #ABB8C2;
}

.post-profile{
    position: relative;
    display: flex;
    align-items: center;    
}

.post-user{
    display: flex;
    flex-direction: column;
    margin-left: 5px;
}
.post-image{       
    width: 50px;
    height: 50px;        
}

.post-text{
    margin-top: 10px;
}

.post-date{
    margin-top: 10px;
    font-size: 12px;
}